// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload, {
  error: './assets/icon/error.png', // 
  loading: './assets/icon/loading.png' // 
})

Vue.config.productionTip = false

/*
* 
*/
Vue.prototype.goBeforeLoginUrl = () => {
  let url = sessionStorage.getItem('beforeLoginUrl')
  console.log(url)
  if(!url || url.indexOf('/author') != -1){
    router.push('/home')
  }else{
    if(url == '/'){
      url = '/home'
    }
    router.push(url)
    sessionStorage.setItem('beforeLoginUrl', '')
  }
}

/*
* 
* urlrouter.beforeEach
*/
router.beforeEach((to, from, next) => {
  if(to.path == '/author' && sessionStorage.getItem('memberInfo')){
    // 
    next('/home')
    return false
  }
  /*
  * sessionStorage
  * 
  */
  if(!sessionStorage.getItem('memberInfo') && to.path !='/author'){
    sessionStorage.setItem('beforeLoginUrl', to.fullPath) // url
    next('/author') // 
    return false
  }else {
    next()
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



