import MyHeader from '../../../components/header/header'
import ConfirmAddress from './son/confirm_address'
import GoodsItem from '../../../components/goodsItem/goodsItem'

const confirmGoodsObj = JSON.parse(sessionStorage.getItem("confirmGoodsObj"))
const memberInfo = JSON.parse(sessionStorage.getItem("memberInfo"))

export default {
	data () {
		return {
			data: JSON.parse(sessionStorage.getItem("confirmGoodsObj")),
			paymentState: true,
			orderJson: {
				member_id: memberInfo.uid, // id
		    address_id: "", // ID
		    deduct_integral_value: "", // 
		    coupon_ids: "", // id,,
		    orderBeans:[{
		      merchants_id: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).merchants_id, // ID
		      order_type: "", // 
		      order_remark: "", // 
		      member_group_id: "", //   
		      orderGoodsBeans:[{
		        goods_id: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).goods_id, // ID
		        specification_id: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).specification_id, // ID
		        goods_num: JSON.parse(sessionStorage.getItem("confirmGoodsObj")).num, //
		        goods_group_id: ""  // ID
		      }]
		    }]
			}
		}
	},
	methods: {
		/*
		* ID
		*/
		getAddressId (id) {
			console.log(id)
			this.orderJson.address_id = id
		},
		/*
		* 
		*/
		submitOrder () {
			console.log(this.orderJson)
		}
	},
  components: {
  	MyHeader,
  	ConfirmAddress,
  	GoodsItem
  }
}



